import { Alert, Button, Input, Spinner } from '@sieloapp/components';
import image from '@sieloapp/shared/images/img18.png';
import {
  StyledWrap,
  StyledImg,
  StyledTitle,
  StyledDesc,
  StyledResetForm,
  StyledBtnWrap,
} from './style';
import { useState } from 'react';
import { SubmitHandler, useForm } from 'react-hook-form';
import { hasKey } from '@sieloapp/shared/methods';
import { CognitoUserPool, CognitoUser } from 'amazon-cognito-identity-js';

interface alertColor {
  alertColor:
    | 'primary'
    | 'secondary'
    | 'success'
    | 'danger'
    | 'warning'
    | 'info'
    | 'light'
    | 'dark'
    | undefined;
}

// interface buttonMessage {
//   buttonMessage: 'Get your reset code' | 'Reset your password';
// }

interface IFormValues {
  email: string;
  verificationCode: string;
  password: string;
}

const PasswordContainer = () => {
  const userPoolAWS = process.env.REACT_APP_COGNITO_USER_POOL_ID as string;
  const clientIdAWS = process.env.REACT_APP_COGNITO_CLIENT_ID as string;
  const [resendingPassword, setreSendingPassword] = useState(false);
  const [resetMessage, setResetPassword] = useState('Get your reset code');
  const [msg, setMessage] = useState('');
  const [alertColor, setAlertColor] = useState(
    'primary' as alertColor['alertColor'],
  );

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<IFormValues>();

  const poolData = {
    UserPoolId: userPoolAWS,
    ClientId: clientIdAWS,
  };
  const userPool = new CognitoUserPool(poolData);

  const onSubmit: SubmitHandler<IFormValues> = (data) => {
    setMessage('');
    setreSendingPassword(true);
    const userData = {
      Username: data.email,
      Pool: userPool,
    };
    const cognitoUser = new CognitoUser(userData);
    if (resetMessage === 'Get your reset code') {
      cognitoUser.forgotPassword({
        onSuccess: (result) => {
          // console.log('Reset password code sent successfully', result);
          setAlertColor('success');
          setMessage(
            'A password reset code has been sent to your email address.',
          );
          setResetPassword('Reset your password');
          setreSendingPassword(false);
        },
        onFailure: (err) => {
          setResetPassword('Get your reset code');
          // console.error('Error while sending reset password code', err);
          setAlertColor('danger');
          setMessage(err.message);
          setreSendingPassword(false);
        },
      });
    } else {
      cognitoUser.confirmPassword(data.verificationCode, data.password, {
        onSuccess: () => {
          setAlertColor('success');
          setMessage('Successfuly changed password. Go to Sign In page');
          setreSendingPassword(false);
          // console.log('Password confirmed and changed successfully');
        },
        onFailure: (err) => {
          setAlertColor('danger');
          setMessage(err.message);
          // console.error('Error confirming and changing password:', err);
          setreSendingPassword(false);
        },
      });
    }
  };

  return (
    <StyledWrap>
      <StyledImg>
        <img src={image} alt="forgot password" />
      </StyledImg>
      <StyledTitle>Reset your password</StyledTitle>
      <StyledDesc>
        Enter your username or email address and we will send to your mailbox a
        code to reset your password.
      </StyledDesc>
      {msg !== '' && <Alert color={alertColor}>{msg}</Alert>}
      {msg !== 'Successfuly changed password. Go to Sign In page' && (
        <form
          action="#"
          style={{ width: '70%' }}
          onSubmit={handleSubmit(onSubmit)}
          noValidate
        >
          <StyledResetForm>
            <Input
              type="email"
              id="email"
              mb="20px"
              placeholder="yourname@yourmail.com"
              feedbackText={errors?.email?.message}
              state={hasKey(errors, 'email') ? 'error' : 'success'}
              showState={!!hasKey(errors, 'email')}
              {...register('email', {
                required: 'Email is required',
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                  message: 'invalid email address',
                },
              })}
            />
            {resetMessage === 'Reset your password' && (
              <>
                <Input
                  type="text"
                  id="verificationCode"
                  placeholder="Verification Code"
                  mb="20px"
                  feedbackText={errors?.verificationCode?.message}
                  state={
                    hasKey(errors, 'verificationCode') ? 'error' : 'success'
                  }
                  showState={!!hasKey(errors, 'verificationCode')}
                  {...register('verificationCode', {
                    required: 'Verification code is required',
                    pattern: {
                      value: /^\d{6}$/,
                      message: 'Invalid verification code',
                    },
                  })}
                />
                <Input
                  id="password"
                  type="password"
                  placeholder="Enter your new password"
                  feedbackText={errors?.password?.message}
                  state={hasKey(errors, 'password') ? 'error' : 'success'}
                  showState={!!hasKey(errors, 'password')}
                  {...register('password', {
                    required: 'Password is required',
                    minLength: {
                      value: 6,
                      message: 'Minimum length is 6',
                    },
                    maxLength: {
                      value: 10,
                      message: 'Minimum length is 10',
                    },
                  })}
                />
              </>
            )}

            <Button type="submit" color="brand2" mt="10px" fullwidth>
              {resendingPassword ? (
                <div className="spin">
                  <Spinner size="xs" color="light" />
                </div>
              ) : (
                <>{resetMessage}</>
              )}
            </Button>
          </StyledResetForm>
        </form>
      )}
      <StyledBtnWrap>
        <Button
          path="mailto:support@sieloapp.com"
          color="white"
          ml="10px"
          mr="10px"
          variant="texted"
        >
          Contact Support
        </Button>
        |
        <Button
          path="/signin"
          color="white"
          ml="10px"
          mr="10px"
          variant="texted"
        >
          Sign In
        </Button>
        |
        <Button path="/signup" color="white" ml="10px" variant="texted">
          Sign Up
        </Button>
      </StyledBtnWrap>
    </StyledWrap>
  );
};

export default PasswordContainer;
